.section-about {
    border-bottom: 2px solid $white;
    display: grid;
    grid-template-columns: 1fr 1fr;
    color: $white;
    row-gap: 70px;

    @media (max-width: $desktop1300-width) {
        row-gap: 35px;
     }

     @media (max-width: $mobile480-width) {
        gap: 10px;
     }
}

.about-heading {
    display: flex;
    flex-direction: column;
    padding: 38px 95px 0px 100px;

    .section-title {
        color: $light-blue;
        margin-bottom: 5px;
    }

    @media (max-width: $desktop1300-width) {
        padding: 30px 50px 0;
     }

     @media (max-width: $tablet-width) {
        grid-column: span 2;
     }

     @media (max-width: $mobile480-width) {
        padding: 20px;
     }
}

.about-text {
    @include fontface-regular;
    font-size: 48px;
    line-height: 58.09px;
    color: $light-blue;
    margin: 0;
    padding: 0;
    margin-bottom: 5px;

    @media (max-width: $desktop1300-width) {
        font-size: 40px;
        line-height: 55px;
     }

     @media (max-width: $tablet-width) {
        font-size: 25px;
        line-height: 25px;
     }
}

.about-services {
    grid-area: 2/1/3/2;
}

.services-list {
    display: flex;
    justify-content: center;
    border-top: 2px solid $white;
    border-bottom: 2px solid $white;

    @media (max-width: $tablet-width) {
        grid-column: span 2;
    }

    @media (max-width: $mobile480-width) {
        flex-wrap: wrap;
    }

}
.services-list__item {
    border-right: 2px solid $white;
    padding: 57px 15px 50px;
    text-align: center;
    width: 100%;
    &:last-child {
        border: none;
    }

    @media (max-width: $desktop1300-width) {
       padding: 30px;
     }

    @media (max-width: $mobile480-width) {
        border-right: none;
        border-bottom: 2px solid $white;

        &:last-child {
            border-bottom: none;
        }
    }
}
.services-list__item-grafity {
    width: 140px;
    height: 140px;
    border: 1px solid $white;
    border-radius: 50%;
    margin: 0 auto 20px;
    display: flex;
    align-items: center;
    justify-content: center;

    span {
        font-family: "Inter-Regular", Arial, Helvetica, sans-serif;
        font-size: 40px;
        font-weight: 400;
        line-height: 48.41px;
        position: relative;

        @media (max-width: $desktop1300-width) {
            font-size: 20px;
         }
    }

    @media (max-width: $desktop1300-width) {
        width: 60px;
        height: 60px;
     }
}
.about-image__item-one {
    padding: 40px 109px 40px 75px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 42px;
    grid-row: span 2;
    border-left: 2px solid $white;
    border-bottom: 2px solid $white;

    img {
        width: 100%;
        height: 100%;
    }

    @media (max-width: $desktop1300-width) {
        padding: 20px;
        justify-content: center;
     }

     @media (max-width: $tablet-width) {
        grid-column: span 2;
     }

    @media (max-width: $mobile480-width) {
        border-left: none;
    }
}
.about-image__item-second {
    display: flex;
    justify-content: center;
    gap: 50px;
    padding: 50px 132px;
    grid-column: span 2;
    border-top: 2px solid $white;
    img {
        width: 100%;
        height: 100%;
    }

    @media (max-width: $desktop1300-width) {
        padding: 20px 50px;
     }

     @media (max-width: $tablet-width) {
        flex-direction: column;
        padding: 10px;
        gap: 10px;
     }
}