.section-teacher-sliders {
    color: $white;
}
.teacher-sliders__item {
    display: flex;
    flex-direction: column;

    .teacher-page-title {
        padding: 38px 0 38px 105px;
        margin: 0;

        @media (max-width: $desktop1300-width) {
            padding-left: 50px;
        }
    }
}
.teacher-page-sliders {
    border-top: 2px solid $white;
    border-bottom: 2px solid $white;
    padding: 45px 200px 20px;

    .professors-link {
        margin-left: 0;
        margin-top: auto;
    }

    @media(max-width: $desktop-width) {
        padding-left: 180px;
        padding-right: 180px;
    }

    @media(max-width: $desktop1300-width) {
        padding-left: 140px;
        padding-right: 140px;
    }

    @media(max-width: $tablet-width) {
        padding-left: 40px;
        padding-right: 40px;
    }

    @media(max-width: $mobile480-width) {
        padding: 20px;
    }

}

.sliders-faculty-one,
.sliders-faculty-second {
    display: flex;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;

    .courses-slider__swiper-navigation {
        margin-left: auto;
        margin-right: 10px;
    }
}


.teacher-page-sliders__list {
    display: flex;
}
.teacher-page-sliders__list-item {
    display: flex;
    flex-direction: column;
}
.teacher-page-sliders__item-image {
    border-radius: 40px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* border: 1px solid $white; */
}
.teacher-page-sliders__item-info {
    border-radius: 40px;
    border: 1px solid $white;
    padding: 25px 15px;
    display: flex;
    flex-direction: column;
    gap: 13px;
    height: 330px;
}
.teacher-page-slider__item-title {
    @include fontface-regular-pro;
    font-size: 24px;
    line-height: 25.31px;
    padding: 0;
    margin: 0;
}
.teacher-page-sliders__item-text {
    @include fontface-regular-pro;
    font-size: 16px;
    line-height: 15.42px;
    padding: 0;
    margin: 0;
}
.teacher-faculty {
    font-weight: 700;
}
.teacher-faculty__list {
    display: flex;
    flex-direction: column;
    padding-left: 20px;
}
.teacher-faculty__list-item {
    list-style-type: disc;
}
