.section-comment-students {
    display: flex;
    flex-direction: column;
}

.comment-students-list {
    padding: 50px 125px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 28px;

    @media (max-width: $tablet900-width) {
        flex-direction: column;
        padding: 50px;
    }

    @media (max-width: $mobile480-width) {
        padding: 20px;
    }
}
.comment-students-list__item {
    display: flex;
    align-items: center;
    justify-content: center;
}
.comment-students__image {
    object-fit: cover;
    width: 100%;
    height: 100%;
}