.section-heading {
    display: flex;
    align-items: center;
    padding: 70px 100px 40px 0;
    border-bottom: 2px solid $white;
    position: relative;

    &::before {
        content: "";
        width: 125px;
        height: 125px;
        background-image: url("../../img/arrow-white.svg");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        position: absolute;
        right: 100px;
        top: calc(50% - 60px);
        z-index: 1;

        @media (max-width: $desktop1300-width) {
            width: 60px;
            height: 60px;
            right: 50px;
            top: calc(50% - 30px);
        }

        @media (max-width: $tablet-width) {
            padding-right: 10px;
            top: initial;
            bottom: 10px;
            left: calc(50% - 30px);
        }
    }

    @media (max-width: $tablet-width) {
        padding-top: 30px;
        padding-right: 10px;
        padding-bottom: 80px;
        justify-content: center;
    }
}

.teacher-name-page {
    .heading-title {
        max-width: 750px;
        font-size: 48px;
        line-height: 50px;

        @media (max-width: $mobile480-width) {
            font-size: 30px;
            line-height: 30px;
        }
    }


}

.heading-title {
    @include fontface-special;
    font-size: 50px;
    line-height: 55.13px;
    color: $white;
    text-transform: uppercase;
    padding: 0;
    margin: 0;
    padding-left: 105px;

    span {
        @include fontface-regular-pro;
        font-size: 40px;
        line-height: 43.21px;
    }

    @media (max-width: $desktop1300-width) {
        padding-left: 50px;
    }

    @media (max-width: $tablet-width) {
        font-size: 30px;
        line-height: 30px;
        padding-left: 30px;

        span {
            font-size: 25px;
            line-height: 30px;
        }
    }
}

.designation-section-title {
    padding: 25px 0 25px 105px;
    margin: 50px 0 0 0;
    @include fontface-regular-pro;
    font-size: 48px;
    line-height: 50.62px;
    text-transform: uppercase;
    background-color: $white;
    color: $black;

    @media (max-width: $desktop1300-width) {
        padding-left: 50px;
    }

    @media (max-width: $tablet900-width) {
        font-size: 38px;
        line-height: 44px;
    }

    @media (max-width: $mobile480-width) {
        font-size: 26px;
        line-height: 34px;
        padding: 20px;
    }
}