.section-faq {
    display: flex;
    flex-direction: column;
}

.accordeon-list {
    display: flex;
    flex-direction: column;
}

.accordion-list__item {
    display: flex;
    flex-direction: column;
    padding: 30px 80px 30px 105px;
    border-bottom: 2px solid $white;

    @media (max-width: $tablet900-width) {
        padding-left: 50px;
    }

    @media (max-width: $tablet-width) {
        padding: 30px;
    }

    @media (max-width: $mobile480-width) {
        padding: 20px;
    }
}

.accordeon-item__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.accordeon-title {
    @include fontface-regular-pro;
    font-size: 36px;
    line-height: 34.45px;
    text-transform: uppercase;
    padding: 0;
    margin: 0;

    @media (max-width: $tablet-width) {
        font-size: 25px;
        line-height: 28px;
    }
}

.accordeon-button {
    background-color: transparent;
    border: none;
    width: 60px;
    height: 60px;
    background-image: url("../img/plus.png");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;
    cursor: pointer;

    @media (max-width: $tablet900-width) {
        width: 35px;
        height: 35px;
    }
}

.accordeon-item__content {
    padding-right: 120px;
    max-height: 0px;
    overflow: hidden;
    transition: max-height 0.5s;

    @media (max-width: $tablet900-width) {
        padding: 0;
    }
}

.accordion-list__item.active .accordeon-item__content {
    max-height: 100px;
    transition: max-height 0.5s ease;
}

.accordion-list__item.active .accordeon-item__header {
    margin-bottom: 20px;
}