.section-missions {
    display: flex;
    flex-direction: column;
    margin-top: 40px;
    color: $white;
    border-top: 2px solid $white;
    padding-bottom: 40px;

    @media (max-width: $mobile480-width) {
        padding-bottom: 0;
    }
}
.section-missions-content {
    display: grid;
    grid-template-columns: 680px 1fr;
    border-bottom: 2px solid $white;

    @media (max-width: $desktop1300-width) {
        grid-template-columns: 1fr 1fr;
    }
}
.section-missions-content__info {

    .section-link {
        margin-left: auto;
    }

    @media (max-width: $desktop1300-width) {
        grid-column: span 2;
        border-bottom: 2px solid $white;
    }

}
.section-missions-content__info-title {
    @include fontface-regular-pro;
    font-size: 64px;
    line-height: 61.25px;
    text-transform: uppercase;
    margin: 0;
    padding: 35px 15px 15px 105px;

    @media (max-width: $desktop1300-width) {
        padding-left: 50px;
    }

    @media (max-width: $tablet900-width) {
        padding: 20px;
        font-size: 45px;
        line-height: 50px;
    }

    @media (max-width: $tablet-width) {
        font-size: 28px;
        line-height: 35px;
    }
}
.section-missions-content__description {
    border-top: 2px solid $white;
    padding: 50px 50px 25px 105px;
    @media (max-width: $desktop1300-width) {
        padding-left: 50px;
    }

    @media (max-width: $tablet900-width) {
        padding: 20px;
    }
}
.section-missions-content__description-title {
    @include fontface-medium;
    font-size: 16px;
    line-height: 19.36px;
    padding: 0;
    margin: 0 0 15px;
    text-transform: uppercase;
}

.section-missions-content__games {
    border-left: 2px solid $white;
    padding: 170px 65px 20px;
    background-image: url("../../img/arrow-white.svg");
    background-repeat: no-repeat;
    background-position: 70px 40px;
    background-size: 80px 80px;

    .section-missions-content__description {
       border: none;
       padding: 0;
    }

    @media (max-width: $desktop1300-width) {
        grid-column: span 2;
        background-image: none;
        padding-top: 20px;
    }

    @media (max-width: $tablet900-width) {
        padding: 20px;
    }
}

.section-missions-content__games-img {

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    margin-bottom: 30px;
}