.course-detail {
    display: flex;
    flex-direction: column;
    color: $white;

    .section-heading {
        &::before {
            display: none;
        }

        @media (max-width: $tablet-width) {
            padding-top: 30px;
            padding-bottom: 30px;
        }
    }
}

.course-detail-information {
    display: flex;
    gap: 25px;
    padding: 60px 60px 60px 105px;
    position: relative;

    @media (max-width: $desktop1300-width) {
        flex-direction: column;
        gap: 0;
    }

    @media (max-width: $tablet900-width) {
        justify-content: center;
        align-items: center;
    }

    @media (max-width: $tablet-width) {
        padding: 30px;
    }

    @media (max-width: $mobile480-width) {
        padding: 20px;
    }

}
.course-detail-information__img {
    width: 438px;
    height: 559px;

    @media (max-width: $desktop1300-width) {
        position: absolute;
        top: 60px;
        left: 105px;
        max-width: 400px;
        height: auto;
    }

    @media (max-width: $tablet900-width) {
        position: sticky;
        max-width: 438px;
        height: auto;
    }

}
.course-detail-information__text {
    @include fontface-extralight;
    font-size: 24px;
    line-height: 29.05px;
        p {
            margin: 10px 0;
        }

    @media (max-width: $desktop1300-width) {
        float: right;
        font-size: 20px;
        line-height: 25px;

        &:before {
            content: "";
            width: 460px;
            height: 560px;
            float: left;
          }
    }

    @media (max-width: $tablet900-width) {
        &::before {
            display: none;
        }
    }

    @media (max-width: $tablet-width) {
        font-size: 16px;
    }

    @media (max-width: $mobile480-width) {
        font-size: 13px;
    }
}